export default {
	friendlyDate: (timestamp) => {
		var formats = {
			'year': '%n% ',
			'month': '%n% ',
			'day': '%n% ',
			'hour': '%n% ',
			'minute': '%n% ',
			'second': '%n% ',
		};
		var now = Date.now();
		var seconds = Math.floor((now - parseInt(timestamp)) / 1000);
		var minutes = Math.floor(seconds / 60);
		var hours = Math.floor(minutes / 60);
		var days = Math.floor(hours / 24);
		var months = Math.floor(days / 30);
		var years = Math.floor(months / 12);

		var diffType = '';
		var diffValue = 0;
		if (years > 0) {
			diffType = 'year';
			diffValue = years;
		} else {
			if (months > 0) {
				diffType = 'month';
				diffValue = months;
			} else {
				if (days > 0) {
					diffType = 'day';
					diffValue = days;
				} else {
					if (hours > 0) {
						diffType = 'hour';
						diffValue = hours;
					} else {
						if (minutes > 0) {
							diffType = 'minute';
							diffValue = minutes;
						} else {
							diffType = 'second';
							diffValue = seconds === 0 ? (seconds = 1) : seconds;
						}
					}
				}
			}
		}
		return formats[diffType].replace('%n%', diffValue);
	},
	timeTodate: (format, timestamp) => {
		
		var myDate = new Date();  
		myDate.getYear(); //(2)  
		myDate.getFullYear(); //(4,1970-????)  
		myDate.getMonth(); //(0-11,01)         // myDate.getMonth()+1;   
		myDate.getDate(); //(1-31)  
		myDate.getDay(); //X(0-6,0)  
		myDate.getTime(); //(1970.1.1)  
		myDate.getHours(); //(0-23)  
		myDate.getMinutes(); //(0-59)  
		myDate.getSeconds(); //(0-59)  
		myDate.getMilliseconds(); //(0-999)  
		myDate.toLocaleDateString(); //  
		var mytime=myDate.toLocaleTimeString(); //  
		myDate.toLocaleString( ); //  

		// 2019-10-01 23:08:35
		var tmpDate = new Date(timestamp);
		var seconds = tmpDate.getSeconds();
		var minutes = ('0' + tmpDate.getMinutes()).substr(-2);
		var hours = tmpDate.getHours();
		var days = ('0' + tmpDate.getDate()).substr(-2);
		var months = ('0' + (tmpDate.getMonth() + 1)).substr(-2);
		var years = tmpDate.getFullYear();
		
		var value = '';
		//  .replace(, )

		return format.replace('Y', years).replace('m', months).replace('d', days).replace('H', hours).replace('i', minutes).replace('s', seconds).replace('index', value);
	}
}
